import { useState } from "react";
import { useFieldArray, useForm } from "react-hook-form";
import { Toaster } from "react-hot-toast";
import rawData from "../src/utils/personalizar.json";
import {
  Step0,
  Step1,
  Step2,
  Step3,
  Step4,
  Steps,
} from "./components/PersonalizarNovo";
import {
  IModalidadePersonalizados,
  PersonalizarFormValues,
} from "./utils/types";
const ORIGINALMODALIDADES = rawData.modalidades;
const formInitialValues: PersonalizarFormValues = {
  items: [],
  nome: "",
  email: "",
  observacoes: "",
  politica_privacidade: true,
};

function App() {
  const {
    control,
    register,
    formState: { errors },
    handleSubmit,
    reset,
  } = useForm({
    defaultValues: formInitialValues,
  });
  const [currentStep, setCurrentStep] = useState(0);
  const [currentModalidade, setCurrentModalidade] = useState(
    ORIGINALMODALIDADES[0]
  );

  const {
    fields: terms,
    append,
    remove,
    update,
  } = useFieldArray({
    control: control,
    name: "items",
    keyName: "itemKey",
  });

  const start = () => {
    setCurrentStep(1);
  };

  const resetAll = () => {
    reset();
    setCurrentStep(0);
  };

  const pickModalidade = (m: IModalidadePersonalizados) => {
    setCurrentModalidade(m);
    setCurrentStep(2);
  };

  const goToResume = () => {
    setCurrentStep(3);
  };

  const confirmRequest = () => {
    setCurrentStep(4);
  };
  return (
    <div className="App">
      <Toaster />
      {currentStep === 0 && <Step0 start={start} />}
      {currentStep > 0 && (
        <>
          <Steps currentStep={currentStep} />
          {currentStep === 1 && <Step1 pickModalidade={pickModalidade} />}
          {currentStep === 2 && (
            <Step2
              currentModalidade={currentModalidade}
              control={control}
              register={register}
              goToResume={goToResume}
              append={append}
              remove={remove}
              update={update}
              terms={terms}
            />
          )}
          {currentStep === 3 && (
            <Step3
              start={start}
              control={control}
              remove={remove}
              confirmRequest={confirmRequest}
            />
          )}
          {currentStep === 4 && (
            <Step4
              register={register}
              errors={errors}
              handleSubmit={handleSubmit}
              reset={resetAll}
            />
          )}
        </>
      )}
    </div>
  );
}

export default App;
