import React, { useState } from "react";
import { Control, UseFormRegister } from "react-hook-form";
import rawData from "../../utils/personalizar.json";
import {
  FormItems,
  IGamaPersonalizados,
  IModalidadePersonalizados,
  PersonalizarFormValues,
} from "../../utils/types";
import { ProductInputRow } from "./ProductInputRow";
const ORIGINALPRODUTOS = rawData.produtos;

interface Step2Pops {
  currentModalidade: IModalidadePersonalizados;
  control: Control<PersonalizarFormValues>;
  register: UseFormRegister<PersonalizarFormValues>;
  goToResume: () => void;
  terms: FormItems[];
  append: Function;
  update: Function;
  remove: Function;
}

export const Step2: React.FC<Step2Pops> = ({
  currentModalidade,
  register,
  goToResume,
  terms,
  append,
  update,
  remove,
}) => {
  const [currentGama, setCurrentGama] = useState<IGamaPersonalizados>(
    ORIGINALPRODUTOS.find((f) => f.slug === currentModalidade.slug)
      ?.gamas[0] as IGamaPersonalizados
  );

  return (
    <div className="px-4 md:px-8 max-w-screen-2xl mx-auto">
      <h2 className="text-xl md:text-2xl font-bold font-roboto uppercase text-center py-8">
        {currentModalidade.nome}
      </h2>
      <div className="grid grid-rows-[auto_auto] lg:grid-cols-6 lg:grid-rows-1">
        <div className="lg:col-span-2 font-roboto font-bold text-2xl flex flex-col items-start">
          {ORIGINALPRODUTOS.find(
            (f) => f.slug === currentModalidade.slug
          )?.gamas?.map((gama) => (
            <button
              key={gama.slug}
              onClick={() => {
                setCurrentGama(gama);
              }}
              className={`px-6 py-3 uppercase hover:bg-black hover:text-white transition-all duration-200 ${
                gama.slug === currentGama?.slug && "bg-black text-white"
              }`}
            >
              {gama.nome}
            </button>
          ))}
        </div>
        <div className="lg:col-span-4 flex flex-col gap-4">
          {currentGama?.tipos &&
            currentGama?.tipos.map((genero) => (
              <div key={genero.tipo} className="mt-8 lg:mt-0">
                <div className="font-bold uppercase font-roboto mb-4">
                  {genero.tipo}
                </div>
                <div className="flex flex-col gap-4">
                  {genero?.produtos &&
                    genero?.produtos.map((p) => (
                      <ProductInputRow
                        currentGama={currentGama}
                        currentModalidade={currentModalidade}
                        genero={genero}
                        produto={p}
                        register={register}
                        key={`${genero.tipo}-${p.slug}`}
                        terms={terms}
                        append={append}
                        update={update}
                        remove={remove}
                      />
                    ))}
                </div>
              </div>
            ))}
          <div className="w-full mt-16 mb-24 flex justify-end">
            <button
              className="personalizar-btn"
              onClick={() => {
                goToResume();
              }}
            >
              Resumo do Pedido
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
