import React from "react";
import { UseFormRegister } from "react-hook-form";
import {
  FormItems,
  IGamaPersonalizados,
  IModalidadePersonalizados,
  IProdutosPersonalizados,
  ITiposPersonalizados,
  PersonalizarFormValues,
} from "../../utils/types";

interface ProductInputRowProps {
  currentModalidade: IModalidadePersonalizados;
  produto: IProdutosPersonalizados;
  genero: ITiposPersonalizados;
  register: UseFormRegister<PersonalizarFormValues>;
  currentGama: IGamaPersonalizados;
  terms: FormItems[];
  append: Function;
  update: Function;
  remove: Function;
}

export const ProductInputRow: React.FC<ProductInputRowProps> = ({
  produto,
  genero,
  terms,
  append,
  update,
  remove,
  register,
  currentModalidade,
  currentGama,
}) => {
  const handleRemoveItem = (
    produto: IProdutosPersonalizados,
    genero: ITiposPersonalizados
  ) => {
    console.log(currentModalidade);

    let findIndex = terms.findIndex(
      (t) =>
        t.produto.slug === produto.slug &&
        t.tipo === genero.tipo &&
        t.modalidade.slug === currentModalidade.slug &&
        t.gama.slug === currentGama.slug
    );
    if (findIndex >= 0) {
      if (Number(terms[findIndex].produto.quantidade) - 1 === 0) {
        remove(findIndex);
      } else {
        update(findIndex, {
          ...terms[findIndex],
          produto: {
            ...terms[findIndex].produto,
            quantidade: Number(terms[findIndex].produto.quantidade) - 1,
          },
        });
      }
    } else {
      return;
    }
  };

  const handleAddItem = (
    produto: IProdutosPersonalizados,
    genero: ITiposPersonalizados
  ) => {
    let findIndex = terms.findIndex(
      (t) =>
        t.produto.slug === produto.slug &&
        t.tipo === genero.tipo &&
        t.modalidade.slug === currentModalidade.slug &&
        t.gama.slug === currentGama.slug
    );
    if (findIndex >= 0) {
      update(findIndex, {
        ...terms[findIndex],
        produto: {
          ...terms[findIndex].produto,
          quantidade: Number(terms[findIndex].produto.quantidade) + 1,
        },
      });
    } else {
      append({
        modalidade: currentModalidade,
        gama: currentGama,
        tipo: genero.tipo,
        produto: { ...produto, quantidade: 1 },
      });
    }
  };

  const handleBlur = (
    produto: IProdutosPersonalizados,
    genero: ITiposPersonalizados
  ) => {
    let findIndex = terms.findIndex(
      (t) =>
        t.produto.slug === produto.slug &&
        t.tipo === genero.tipo &&
        t.modalidade.slug === currentModalidade.slug &&
        t.gama.slug === currentGama.slug
    );
    if (findIndex > 0) {
      if (Number(terms[findIndex].produto.quantidade) === 0) {
        remove(findIndex);
      }
    }
  };

  const handleFocus = (
    produto: IProdutosPersonalizados,
    genero: ITiposPersonalizados
  ) => {
    let findIndex = terms.findIndex(
      (t) =>
        t.produto.slug === produto.slug &&
        t.tipo === genero.tipo &&
        t.modalidade.slug === currentModalidade.slug &&
        t.gama.slug === currentGama.slug
    );
    if (findIndex < 0) {
      append({
        modalidade: currentModalidade,
        gama: currentGama,
        tipo: genero.tipo,
        produto: { ...produto, quantidade: 0 },
      });
    }
  };

  return (
    <div
      key={`${genero.tipo}-${produto.slug}`}
      className="border p-4 border-gray-300 flex justify-between items-center"
    >
      <div className="uppercase font-roboto">{produto.nome}</div>
      <div className="flex">
        <button
          className="w-5 h-5 flex items-center justify-center border border-black text-xl font-roboto hover:bg-black hover:text-white transition-all duration-300"
          onClick={() => {
            handleRemoveItem(produto, genero);
          }}
        >
          -
        </button>
        <div className="w-8 text-center font-roboto">
          <input
            className="w-8 text-center font-roboto"
            key={
              terms.find(
                (t) =>
                  t.produto.slug === produto.slug &&
                  t.tipo === genero.tipo &&
                  t.modalidade.slug === currentModalidade.slug &&
                  t.gama.slug === currentGama.slug
              )?.itemKey
            }
            defaultValue={0}
            onFocus={() => {
              handleFocus(produto, genero);
            }}
            {...register(
              `items.${terms.findIndex(
                (t) =>
                  t.produto.slug === produto.slug &&
                  t.tipo === genero.tipo &&
                  t.modalidade.slug === currentModalidade.slug &&
                  t.gama.slug === currentGama.slug
              )}.produto.quantidade`,
              {
                onBlur: () => {
                  handleBlur(produto, genero);
                },
              }
            )}
          />
        </div>
        <button
          className="w-5 h-5 flex items-center justify-center border border-black text-xl font-roboto hover:bg-black hover:text-white transition-all duration-300"
          onClick={() => {
            handleAddItem(produto, genero);
          }}
        >
          +
        </button>
      </div>
    </div>
  );
};
