import axios from "axios";
import { useState } from "react";
import {
  FieldErrors,
  UseFormHandleSubmit,
  UseFormRegister,
  UseFormReset,
} from "react-hook-form";
import toast from "react-hot-toast";
import { BsInfoCircle } from "react-icons/bs";
import { PersonalizarFormValues } from "../../utils/types";
import { Spinner } from "../Spinner";

interface Step4Props {
  register: UseFormRegister<PersonalizarFormValues>;
  errors: FieldErrors<PersonalizarFormValues>;
  handleSubmit: UseFormHandleSubmit<PersonalizarFormValues>;
  reset: UseFormReset<PersonalizarFormValues>;
}

export const Step4: React.FC<Step4Props> = ({
  register,
  errors,
  handleSubmit,
  reset,
}) => {
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const submitPersonalizados = async (data: PersonalizarFormValues) => {
    console.log(data);
    try {
      setIsLoading(true);
      await axios.post(
        "https://pacto.cc/wp-json/personalizados/send-email",
        { ...data },
        {
          headers: { Authorization: "Bearer webaniceday" },
        }
      );
      toast.success(
        "Pedido recebido. Ser contactado o mais breve possvel. Obrigado."
      );
      reset();
      setIsLoading(false);
    } catch (err) {
      setIsLoading(false);
      console.log(err);
      toast.error(
        "Ups! Erro no pedido! Por favor contacte-nos via email para store@pacto.cc"
      );
    }
  };

  return (
    <div className="px-4 md:px-8 max-w-screen-2xl mx-auto">
      <div className="flex justify-center">
        <div className="max-w-sm flex justify-center items-center flex-col space-y-6">
          <h2 className="text-xl md:text-2xl font-bold font-roboto uppercase md:text-center">
            Informao de contacto
          </h2>
          <p className="font-garamond text-lg text-center">
            A nossa proposta de oramento ser enviada o mais breve possvel
            para o email abaixo indicado.
          </p>
          <form
            onSubmit={handleSubmit(submitPersonalizados)}
            className="flex flex-col w-full px-4 space-y-6 font-roboto"
          >
            <div className="flex flex-col w-full">
              <label htmlFor="" className="text-xs uppercase">
                Nome *
              </label>
              <input
                className={`border px-3 py-2 bg-gray-50 ${
                  errors.nome ? "border-red-500" : ""
                }`}
                {...register("nome", { required: true })}
              />
              {errors.nome && (
                <span className="text-xs text-red-600  flex space-x-2 pt-1">
                  <BsInfoCircle />{" "}
                  <span>Este campo  de preenchimento obrigatrio.</span>
                </span>
              )}
            </div>
            <div className="flex flex-col w-full">
              <label htmlFor="" className="text-xs uppercase">
                Email *
              </label>
              <input
                className={`border px-3 py-2 bg-gray-50 ${
                  errors.email ? "border-red-500" : ""
                }`}
                {...register("email", { required: true })}
              />
              {errors.email && (
                <span className="text-xs text-red-600 flex space-x-2 pt-1">
                  <BsInfoCircle />{" "}
                  <span>Este campo  de preenchimento obrigatrio.</span>
                </span>
              )}
            </div>
            <div>
              <div className="flex flex-col w-full">
                <label htmlFor="" className="text-xs uppercase">
                  Observaes
                </label>
                <textarea
                  rows={4}
                  className={`border px-3 py-2 resize-none bg-gray-50 ${
                    errors.observacoes ? "border-red-500" : ""
                  }`}
                  {...register("observacoes")}
                />
                {errors.observacoes && (
                  <span className="text-xs text-red-600">
                    Este campo  de preenchimento obrigatrio.
                  </span>
                )}
              </div>
              <p className="text-xs pt-4 font-roboto">
                *Campos de preenchimento obrigatrio.
              </p>
            </div>
            <div className="flex flex-col space-y-4">
              <div>
                <div className=" flex flex-row space-x-3">
                  <input
                    type="checkbox"
                    {...register("politica_privacidade", {
                      required:
                        "Obrigatrio aceitar a nossa Poltica de Privacidade",
                    })}
                  />
                  <p className="text-xs font-roboto font-bold">
                    Li e concordo com a{" "}
                    <a
                      href="/politica-de-privacidade-e-cookies/"
                      className="underline"
                    >
                      Poltica de Privacidade
                    </a>
                  </p>
                </div>
                {errors.politica_privacidade && (
                  <span className="text-xs text-red-600 flex space-x-2 pt-1">
                    <BsInfoCircle />{" "}
                    <span>Este campo  de preenchimento obrigatrio.</span>
                  </span>
                )}
              </div>
              <div className="flex justify-center pt-8">
                <button className="personalizar-btn" type="submit">
                  {isLoading && (
                    <div className="absolute inset-0 bg-white z-10 flex items-center justify-center transition border border-black">
                      <Spinner />
                    </div>
                  )}
                  Pedir Oramento
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};
